:root {
  --color-primary-500: #816cc5;
  --color-neutral-900: #616161;
  --color-neutral-700: #a5a5a5;
  --color-neutral-500: #e8e8e8;
  --color-black: #000000;
  --color-white: #ffffff;
  --font-h1: "Karla", sans-serif;
  --size-h1: 40px;
  --line-h1: auto;
  --weight-h1: 600;
  --font-h2: "Karla", sans-serif;
  --size-h2: 33px;
  --line-h2: auto;
  --weight-h2: 600;
  --font-h3: "Karla", sans-serif;
  --size-h3: 25px;
  --line-h3: auto;
  --weight-h3: 600;
  --font-h4: "Karla", sans-serif;
  --size-h4: 16px;
  --line-h4: auto;
  --weight-h4: 600;
  --font-h5: "Karla", sans-serif;
  --size-h5: 14px;
  --line-h5: auto;
  --weight-h5: 600;
  --font-h6: "Karla", sans-serif;
  --size-h6: 12px;
  --line-h6: auto;
  --weight-h6: 600;
  --font-body: "Karla", sans-serif;
  --size-body: 16px;
  --line-body: auto;
  --weight-body: 400;
  --font-terms: "Karla", sans-serif;
  --size-terms: 32px;
  --line-terms: auto;
  --font-captions: "Karla", sans-serif;
  --size-captions: 8px;
  --line-captions: auto;
  --font-logo: "Karla", sans-serif;
  --size-logo: 46px;
  --line-logo: auto;
  --font-hamburger: "Karla", sans-serif;
  --size-hamburger: 20px;
  --line-hamburger: auto;
}
.h1 {
  font-family: var(--font-h1);
  font-size: var(--size-h1);
  line-height: var(--line-h1);
  font-weight: var(--weight-h1);
}
.h2 {
  font-family: var(--font-h2);
  font-size: var(--size-h2);
  line-height: var(--line-h2);
  font-weight: var(--weight-h2);
}
.h3 {
  font-family: var(--font-h3);
  font-size: var(--size-h3);
  line-height: var(--line-h3);
  font-weight: var(--weight-h3);
}
.h4 {
  font-family: var(--font-h4);
  font-size: var(--size-h4);
  line-height: var(--line-h4);
  font-weight: var(--weight-h4);
}
.h5 {
  font-family: var(--font-h5);
  font-size: var(--size-h5);
  line-height: var(--line-h5);
  font-weight: var(--weight-h5);
}
.h6 {
  font-family: var(--font-h6);
  font-size: var(--size-h6);
  line-height: var(--line-h6);
  font-weight: var(--weight-h6);
}
.body-copy {
  font-family: var(--font-body);
  font-size: var(--size-body);
  line-height: var(--line-body);
  font-weight: var(--weight-body);
}
.terms-of-service {
  font-family: var(--font-terms);
  font-size: var(--size-terms);
  line-height: var(--line-terms);
}
.captions {
  font-family: var(--font-captions);
  font-size: var(--size-captions);
  line-height: var(--line-captions);
}
.logo {
  font-family: var(--font-logo);
  font-size: var(--size-logo);
  line-height: var(--line-logo);
}
.hamburger-text {
  font-family: var(--font-hamburger);
  font-size: var(--size-hamburger);
  line-height: var(--line-hamburger);
}
body {
  background: #fff;
  color: #000;
  font-family: var(--font-h1);
}
.heading-main {
  font-family: var(--font-h1);
  font-size: var(--size-h1);
  font-weight: var(--weight-h1);
  line-height: var(--line-h1);
}
.create-post-container {
  min-height: 100vh;
  background: #fff;
  padding: 24px 16px;
  max-width: 400px;
  margin: 0 auto;
  position: relative;
}
.create-post-content {
  padding-bottom: 120px;
}
.header-row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 8px;
  margin-top: 32px;
}
.back-button-container {
  cursor: pointer;
  width: 24px;
  height: 32px;
  display: flex;
  align-items: center;
}
.create-post-header {
  flex: 1;
  text-align: center;
  color: #000;
  font-size: 25px;
  margin: 0;
  margin-left: -24px;
}
.add-image-title {
  text-align: center;
  color: #000;
  font-size: 1.1rem;
  font-weight: 500;
  margin-bottom: 8px;
}
.create-captions-title {
  color: #000;
  font-size: 1rem;
  font-weight: 600;
  margin: 18px 0 6px 0;
}
.create-post-button-bar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 16px 16px;
  z-index: 99999;
  max-width: 400px;
  margin: 0 auto;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
}
.splash-container {
  min-height: 100vh;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 8px solid #F2F0F9;
  box-sizing: border-box;
}
.splash-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 24px;
  color: #000;
}
.splash-button-wrapper {
  width: 180px;
}
.create-post-button {
  font-size: 1.25rem;
  width: 100%;
  padding: 12px 24px;
  background: #816CC5;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
}
.filter-label {
  font-weight: bold;
  margin: 0;
  font-family: "Karla", sans-serif;
  color: var(--color-black);
  max-width: 430px;
  width: 100%;
  margin-inline: auto;
  height: 100vh;
  box-sizing: border-box;
  overflow-x: hidden;
}
.jobsMain {
  background: rgb(255, 255, 255);
}
.jobsCardsContainer {
  margin: 16px 0 10px 0;
  display: flex;
  flex-direction: column;
  gap: 0px;
}
.jobsContainer {
  max-width: 440px;
  width: 100%;
  margin-inline: auto;
  position: relative;
}
.jobsTopBar {
  display: flex;
  align-items: center;
  gap: 4px;
  margin-bottom: 5px;
  padding: 0;
}
.jobsSearchBar {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
}
.jobsSearchIcon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-55%);
  width: 22px;
  height: 22px;
  pointer-events: none;
  opacity: 0.7;
}
.jobsSearchInput {
  flex: 1;
  border-radius: 5px;
  border: 2px solid var(--color-neutral-900);
  padding: 8px 20px 8px 40px;
  font-size: var(--size-body);
  outline: none;
}
.jobsPlusButton {
  background: transparent;
  border: none;
  padding: 0;
  margin-left: 10px;
}
.jobsMessagesButton {
  background: transparent;
  border: none;
  padding: 0;
}
.jobsTabs {
  display: flex;
  gap: 5px;
  margin-bottom: 12px;
  padding: 0;
  overflow-x: auto;
  white-space: nowrap;
}
.jobsTab {
  background: #eee;
  color: #888;
  border: none;
  border-radius: 10px;
  padding: 8px 14px;
  font-weight: 400;
  font-size: var(--size-body);
  cursor: pointer;
  flex-shrink: 0;
  margin-right: 5px;
  display: flex;
  align-items: center;
  gap: 8px;
}
.jobsTabSelected {
  background: var(--color-primary-500);
  color: var(--color-white);
  box-shadow: 0 2px 0 var(--color-primary-500);
}
.jobsSubtabs {
  display: flex;
  margin-bottom: 16px;
  border: 2px solid var(--color-primary-500);
  border-radius: 4px;
  overflow: hidden;
  font-weight: 800;
  font-size: var(--size-body);
}
.jobsSubtab {
  flex: 1;
  background: var(--color-white);
  color: var(--color-primary-500);
  border: none;
  padding: 10px 0;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.2s ease;
}
.jobsSubtabSelected {
  background: var(--color-primary-500);
  color: var(--color-white);
}
.jobsFilterRow {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  padding: 0;
}
.jobsFilterIcon {
  width: 24px;
  height: 24px;
  color: transparent;
  padding: 0;
  cursor: pointer;
  margin-bottom: -20px;
}
.jobDetailContainer {
  max-width: 440px;
  margin: 0 auto;
  padding: 24px;
}
.jobDetailTitle {
  font-size: var(--size-h2);
  font-weight: 700;
  margin-bottom: 4px;
}
.jobDetailCompany {
  font-weight: 600;
  font-size: var(--size-h3);
  margin-bottom: 4px;
}
.jobDetailLocation {
  color: var(--color-neutral-900);
  font-size: var(--size-body);
  margin-bottom: 4px;
}
.jobDetailPosted {
  color: var(--color-neutral-700);
  font-size: var(--size-body);
  margin-bottom: 16px;
}
.jobDetailApplyButton {
  width: 100%;
  background: var(--color-primary-500);
  color: var(--color-white);
  border: none;
  border-radius: 14px;
  padding: 14px 0;
  font-weight: 600;
  font-size: var(--size-body);
  margin-bottom: 24px;
  box-shadow: 0 2px 0 var(--color-primary-500);
  cursor: pointer;
}
.jobDetailDivider {
  margin: 24px 0;
}
.jobDetailSectionHeader {
  font-size: var(--size-h3);
  font-weight: 700;
  margin-bottom: 12px;
}
.jobDetailDescription {
  font-size: var(--size-body);
}
.jobDetailBackButton {
  display: flex;
  align-items: center;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  margin-bottom: 16px;
}
.jobDetailBackIcon {
  width: 40px;
  height: 40px;
}
.jobsHamburgerFixed {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 440px;
  z-index: 1000;
  background: rgb(255, 255, 255);
}
.jobsCardLink {
  text-decoration: none;
  color: inherit;
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
}
.jobsFilterLabel {
  font-weight: 500;
  font-size: 25px;
}
.jobsHamburgerSpacer {
  height: 100px;
  width: 100%;
}
.jobsCreatePostIcon {
  width: 40px;
  height: auto;
}
.jobsMessagesIcon {
  width: 45px;
  height: auto;
}
.filterTag {
  background: #e0e0e0;
  color: #888;
  border-radius: 8px;
  font-weight: 600;
  font-size: 17px;
  padding: 4px 18px;
  margin-right: 8px;
  margin-bottom: -20px;
  display: inline-block;
  font-family: inherit;
  letter-spacing: 0.5px;
  opacity: 0.95;
}
.filterLabel {
  font-weight: 700;
  font-size: 25px;
  margin-right: 8px;
  margin-bottom: -20px;
  display: inline-block;
  color: #111;
}
.hamburgerMenuContainer {
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 440px;
  height: 98px;
  z-index: 1000;
  background-color: var(--color-white);
  transition: all 0.3s ease-in-out;
  overflow: visible;
}
.hamburgerMenuContainer.open {
  width: 338px;
  height: 100vh;
}
.hamburgerMenuButtonContainer {
  margin-top: 38px;
  margin-left: 0;
  z-index: 1002;
  transition: margin-left 0.3s ease-in-out;
}
.hamburgerMenuContainer.open .hamburgerMenuButtonContainer {
  margin-left: 0;
}
.hamburgerMenuButton {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
  border-radius: 8px;
}
.hamburgerMenuButton:hover {
  transform: scale(1.1);
}
.hamburgerMenuIcon {
  width: 44px;
  height: 44px;
  padding: 0;
}
.menuItems {
  width: 100%;
  height: 100vh;
  background-color: var(--color-white);
  padding-top: 30px;
  padding-bottom: 20px;
  z-index: 1002;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}
.hamburgerMenuContainer.open .menuItems {
  opacity: 1;
  visibility: visible;
}
.menuItem {
  padding: 5px 20px;
  color: var(--color-black);
  text-decoration: none;
  font-family: var(--font-body);
  font-size: var(--size-hamburger);
  font-weight: 500;
  display: block;
  transition: background-color 0.2s ease;
}
.menuItem:hover {
  background-color: var(--color-neutral-500);
}
.menuItemContent {
  display: flex;
  align-items: center;
  gap: 12px;
}
.menuItemIcon {
  width: 24px;
  height: 24px;
  object-fit: contain;
}
.hamburgerMenuOverlay {
  position: absolute;
  width: 98px;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
  top: 0;
  left: 338px;
}
.hamburgerMenuOverlay.open {
  opacity: 1;
  visibility: visible;
  z-index: 998;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.menuHeaderRow {
  display: flex;
  align-items: center;
  height: 98px;
  padding: 0;
  justify-content: space-between;
}
.profileSection {
  display: flex;
  margin-top: 50px;
  align-items: center;
  gap: 12px;
  opacity: 1;
  transition: opacity 0.3s ease;
}
.profilePic {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  object-fit: cover;
}
.profileName {
  font-family: var(--font-h3);
  font-size: var(--size-h3);
  font-weight: 600;
  color: var(--color-black);
}
.subMenuGroup {
  margin-left: 32px;
  border-left: 2px solid var(--color-neutral-300);
  padding-left: 12px;
  padding-bottom: 10px;
}
.logoutButton {
  position: absolute;
  left: 0;
  bottom: 32px;
  width: 100%;
  background: none;
  border: none;
  color: var(--color-black);
  font-family: var(--font-body);
  font-size: var(--size-body);
  text-align: left;
  padding: 0 0 0 48px;
  cursor: pointer;
  transition: color 0.2s;
  z-index: 1003;
}
.logoutButton:hover {
  color: var(--color-primary);
}
